.burger__open-buy {
  opacity: 0;
  height: 0;
  color: #fff;
  text-transform: none;
  gap: 15px;
  padding-top: 10px;
  padding-left: 30px;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: opacity 1s;
  display: grid;
}

.menu__ul-li--down {
  height: 42px;
  transition: height 1s;
  position: relative;
  overflow: hidden;
}

.menu__ul-li--down:focus > .burger__open-buy, .menu__ul-li--down:hover > .burger__open-buy {
  height: auto;
  opacity: 1;
}

.menu__ul-li--down:focus, .menu__ul-li--down:hover {
  height: 132px;
}

.buy__images {
  margin-left: 18px;
  transition: transform 1s;
  transform: rotate(0deg);
}

.menu__ul-li--down:focus .buy__images, .menu__ul-li--down:hover .buy__images {
  transform: rotate(180deg);
}

.menu__ul-li--buy-img {
  align-items: center;
  display: flex;
}

.button__menu-image-2, .button__menu--close .button__menu-image-1 {
  display: none;
}

.button__menu--close .button__menu-image-2 {
  display: block;
}

